const dtEvents = [];

// Decorator @OriginalEvent
// Inclui o evento em dtEvents com os valores informados no decorator
function OriginalEvent(value: {
  name: string,
  cancelable?: boolean,
  detail: Array<string>
} = {
  name: '',
  cancelable: true,
  detail: []
}) {
  return function (_component, target) {
    dtEvents.push({...value, emitterName: target});
  };
}


